@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:200&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.chatmain {
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

* {
  margin: 0;
  padding: 0;
}

.home {
  display: flex;
  align-items: center;
}

.hero {
  min-width: 1000px;
  display: flex;
  justify-content: center;
}
.hero video {
  max-width: 90%;
}

.maincard img {
  width: 300px;
  border-radius: 10px 10px 0px 0px;
}

.title {
  font-size: 20px;
  text-align: center;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 700;
}

.maincard {
  margin: 10px;
  border: 1px rgba(182, 202, 252, 0.582) solid;
  border-radius: 10px;
}

.card-wrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 10px;
  height: 100vh;
}

.wrapper {
  width: 100vw;
  background-image: url(/profile/static/media/portfolio-bg.d06ee94f.png);
  background-size: cover;
}

.main {
  width: 100%;
}

.contact {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url(/profile/static/media/background-contact.d5f489c5.png);
  background-size: 110%;
}
.contact h1 {
  padding-bottom: 40px;
  color: skyblue;
}
.contact h5 {
  padding-top: 5px;
  padding-bottom: 6px;
}

.form {
  height: 300px;
  width: 700px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  color: navy;
}

.form input {
  border-radius: 5px;
  outline: none;
  height: 20px;
  border: 1px skyblue solid;
}

.form textarea {
  border-radius: 5px;
  outline: none;
  border: 1px skyblue solid;
}

.btn {
  padding: 10px;
  border-radius: 5px;
  width: 100px;
  border: 2px solid #2c3e50;
  color: #2c3e50;
  display: block;
  font-family: "trebuchet ms";
  font-size: 10px;
  text-decoration: none;
  text-transform: uppercase;
  margin: 10px;
  outline: none;
}

.btn:active {
  outline: none;
}

.navbar {
  display: flex;
  flex-direction: column;
  min-width: 200px;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.links {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.a {
  text-decoration: none;
  font-size: 20px;
  padding: 5px;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 700;
  color: rgb(73, 151, 245);
}

* {
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  flex-direction: column;
}
.main {
  display: flex;
  flex-direction: row;
}

